import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../_guard/auth.guard';
import { DiabosApppsComponent } from './diabos-appps.component';
import { LoginAppCardsComponent } from '../shared/login-app-cards/login-app-cards.component';

const routes: Routes = [
  {
    path: '', component: DiabosApppsComponent, canActivate: [AuthGuard],
    children: [
      {
        path: '',
        children: [
          { path: '', component: LoginAppCardsComponent }
        ]
      }
    ]
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class DiabosAppsRoutingModule { }
