import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { formatDate } from '@angular/common';

import { environment } from '../../../environments/environment';
import { AuthService } from '../../_service/auth.service';
import { UserRole } from '../../_class/userRole';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'app-login-app-cards',
  templateUrl: './login-app-cards.component.html',
  styleUrls: ['./login-app-cards.component.scss']
})
export class LoginAppCardsComponent implements OnInit {

  staticContentServer = environment.staticContentServer;
  isLoggedIn = true;

  email: string;
  userName: string;
  appsAccess: any;
  accessToken: string;
  sessionId: string;
  userRole: UserRole;
  companyName: string;
  companyId: string;
  profilePhoto: string;

  custCode: string;
  userCode: string;

  cardData = [
    {
      title: 'Charter Party Writing',
      subtitle: 'Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diamnonummy nibh …',
      img: '../../../assets/img/cp-writing.svg',
      url: this.staticContentServer + '/charter-party',
      appName: 'CP'

    },
    {
      title: 'Disbursement Accounting',
      subtitle: 'Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diamnonummy nibh …',
      img: '../../../assets/img/disbursement-accounting.svg',
      url: this.staticContentServer + '/disbursement-accounting',
      appName: 'DA'
    },
    {
      title: 'Marine Directory',
      subtitle: 'Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diamnonummy nibh …',
      img: '../../../assets/img/marine-directory.svg',
      url: this.staticContentServer + '/marine-directory',
      appName: 'MD'

    },
    {
      title: 'Smart Agent',
      subtitle: 'Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diamnonummy nibh …',
      img: '../../../assets/img/smart-agent.svg',
      url: this.staticContentServer + '/smart-agent',
      appName: 'SA'
    },
    {
      title: 'Laytime Calculator',
      subtitle: 'Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diamnonummy nibh …',
      img: '../../../assets/img/laytime-calculator.svg',
      url: this.staticContentServer + '/laytime-calculation',
      appName: 'LT'
    },
    {
      title: 'Cash Management',
      subtitle: 'Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diamnonummy nibh …',
      img: '../../../assets/img/cm.svg',
      url: this.staticContentServer + '/cash-management',
      appName: 'CM'
    },
  ];

  constructor(private titleService: Title, private router: Router, private authService: AuthService, private httpClient: HttpClient) { }

  ngOnInit() {
    this.titleService.setTitle('Diabos Apps');
    this.isLoggedIn = this.authService.isLoggedIn;

    if (this.isLoggedIn) {

      this.email = this.authService.email;

      this.userName = this.authService.userName;
      this.appsAccess = this.authService.appsAccess;
      this.accessToken = this.authService.accessToken;
      this.sessionId = this.authService.sessionId;
      this.userRole = this.authService.userRole;
      this.userCode = this.authService.userCode;
      this.companyName = this.authService.companyName;
      this.companyId = this.authService.companyId;
      this.profilePhoto = this.authService.profilePhoto;
    }
  }

  navigate(url, appName) {
    if (this.isLoggedIn && this.userRole && this.appsAccess.indexOf(appName) !== -1) {

      url = environment[appName];
      if (environment.local) {
        if (appName === 'CP') {
          url = 'http://localhost:4200';
        }
      }
    }
    this.authService.openedAppWindows.push({
      win: window.open(url),
      url: url
    });
  }
}
